import React from "react";
import { Route } from "react-router-dom";
import "./App.css";

import Header from './components/Header'
import Home from "./pages/Home/Home";
import AboutMe from "./pages/AboutMe/AboutMe";
import Projects from "./pages/Projects/Projects";
import Contact from "./pages/Contact/Contact";
import Juaniflix from "./pages/Projects/Juaniflix";
import MarioLuigi from "./pages/Projects/MarioLuigi";
import SimonSays from "./pages/Projects/SimonSays";
import CovidApi from "./pages/Projects/CovidApi";

export default function App() {

  return (
      <div className="App">
        <Header/>
        <Route exact path="/my-app" component={Home} />
        <Route exact path="/my-app/about-me" component={AboutMe} />
        <Route exact path="/my-app/projects" component={Projects} />
        <Route exact path="/my-app/projects/juaniflix" component={Juaniflix} />
        <Route exact path="/my-app/projects/mario-vs-luigi" component={MarioLuigi} />
        <Route exact path="/my-app/projects/simon-says" component={SimonSays} />
        <Route exact path="/my-app/projects/covid-live-data" component={CovidApi} />
        <Route exact path="/my-app/contact" component={Contact} />
      </div>
  );
}
