import React, { useState } from "react";
import { Link } from "react-router-dom";
import "./styles.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faBars, faTimes } from "@fortawesome/free-solid-svg-icons";

export default function Header() {
  const [showMenu, setShowMenu] = useState(false);
  let movilMenu;

  if (showMenu) {
    movilMenu = (
      <div className="movil-menu">
        <ul>
          <li onClick={() => setShowMenu(false)}>
            <Link to="/my-app" className="link">
              HOME
            </Link>
          </li>
          <li onClick={() => setShowMenu(false)}>
            <Link to="/my-app/about-me" className="link">
              ABOUT ME
            </Link>
          </li>
          <li onClick={() => setShowMenu(false)}>
            <Link to="/my-app/projects" className="link">
              PROJECTS
            </Link>
          </li>
          <li onClick={() => setShowMenu(false)}>
            <Link to="/my-app/contact" className="link">
              CONTACT
            </Link>
          </li>
        </ul>
      </div>
    );
  }

  return (
      <div className="Header">
        <ul className="menu">
          <li>
            <Link to="/" className="link">
              HOME
            </Link>
          </li>
          <li>
            <Link to="/my-app/about-me" className="link">
              ABOUT ME
            </Link>
          </li>
          <li>
            <Link to="/my-app/projects" className="link">
              PROJECTS
            </Link>
          </li>
          <li>
            <Link to="/my-app/contact" className="link">
              CONTACT
            </Link>
          </li>
        </ul>
      <FontAwesomeIcon
        icon={faBars}
        size="2x"
        aria-hidden="true"
        className="bars"
        onClick={() => setShowMenu(!showMenu)}
        />
      <FontAwesomeIcon
        icon={faTimes}
        size="2x"
        aria-hidden="true"
        className="times"
        onClick={() => setShowMenu(false)}
      />

       {movilMenu}
        </div>
  );
}
